/* global styling */
* {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #3a3a3a;
  background: #fff;
}

p {
  line-height: 2;
  font-size: 1em;
}

body,
header,
main,
footer,
article,
section,
figure,
ul {
  margin: 0;
}

#main-header {
  padding: 1em 0 1em;
  margin-bottom: 1em;
  text-align: center;
}

h1,
h2,
h3,
h4,
p {
  margin: 0 0 1rem;
}

hr {
  margin: 1.5em 0;
}

/* global layout */
.inner {
  padding: 0 1.2em;
}

.container {
  margin: 0 auto;
  max-width: 1600px;
}

/* links */
a {
  text-decoration: none;
  color: #3e77e2;
  transition: color .1s ease-in;
}

a:hover {
  color: #81a6ec;
}

blockquote {
  border-left: 6px solid #6e8063;
  margin: 2.6em 0;
  background: rgba(218, 200, 177, 0.2);
  padding: 20px;
  border-top-left-radius: 30px;
  border-bottom-right-radius: 30px;
  max-width: 480px;
}

blockquote p {
  color: #888;
  font-style: italic;
}

blockquote .author {
  text-decoration: underline;
  font-style: italic;
}

.about-me .inner {
  display: flex;
  flex-direction: column;
}

.about-me .inner h2 {
  order: 1;
}

.about-me .inner .profile-pic {
  width: 210px;
  height: 200px;
  border-radius: 100%;
  display: block;
  margin: 1em auto 2em;
  order: 0;
}

.about-me .inner p {
  order: 1;
}

.about-me__list ul {
  list-style-type: none;
  padding-left: 0;
}

.about-me__list ul li {
  margin-bottom: 1.4em;
  list-style-position: inside;
}

/* social icons */
.social-icons {
  display: flex;
  justify-content: space-around;
  background: #d5d5d5;
  padding: 1em 0;
  border-radius: 1px;
  margin: 0 auto 2em;
}

.social-icons a {
  color: inherit;
  transition: opacity .12s ease-in;
}

.social-icons a:hover {
  opacity: .60;
}

.social-icons .icon {
  font-size: 3rem;
}

/* featured section styles */
/* featured section - header styles */
.featured__header {
  margin-bottom: 2em;
}

.featured__header-left {
  margin-bottom: 2em;
}

.featured__header-left .tech-used ul {
  color: #888;
  margin: 1em 0;
  padding-left: 30px;
}

.featured__header-left .tech-used ul li {
  margin-bottom: .55em;
}

.featured__header-right .inner {
  padding: 0;
}

.featured__header-right img {
  width: 100%;
  height: 220px;
}

/* featured project styles */
.featured-projects {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.featured-projects__module {
  margin-bottom: 2em;
}

.featured-projects__image {
  height: 325px;
  width: 100%;
  margin-bottom: 1.5em;
  border: 2px dashed #f0ecec;
  border-radius: 2px;
  padding: 4px;
  transition: border-color 0.2s ease-in;
}

.featured-projects__image:hover {
  border-color: #97b5ef;
}

.featured-projects__image img {
  height: 100%;
  width: 100%;
}

.featured-projects__caption p {
  color: #888;
}

/* media queries */
@media screen and (min-width: 450px) {
  /* featured header styles */
  .featured__header-right .inner {
    padding: 0 1.2em;
  }
  .featured__header-right img {
    max-width: 450px;
  }
  /* featured project styles */
  .featured-projects__image {
    max-width: 450px;
  }
}

@media screen and (min-width: 645px) {
  #main-header {
    text-align: left;
  }
  .about-me {
    margin-bottom: 2em;
  }
  .about-me .inner {
    flex-direction: column;
  }
  .about-me .inner .profile-pic {
    width: 210px;
    height: 200px;
    border-radius: 100%;
    display: inline-block;
    margin-left: 0;
    margin-right: 0;
  }
  .about-me .inner .about-content p {
    width: 100%;
  }
  .about-me .inner .about-content blockquote {
    margin-left: 1em;
  }
}

/* END of  min-width 450px styles */
@media screen and (min-width: 880px) {
  .featured-projects__module .inner {
    display: flex;
  }
  .featured-projects__module .inner .featured-projects__image {
    margin-right: 4em;
    min-width: 450px;
  }
}

/* END of  min-width 880px styles */
@media screen and (min-width: 1152px) {
  .about-me .inner {
    flex-direction: row;
    justify-content: space-between;
  }
  .about-me .inner .profile-pic {
    margin-right: 3em;
  }
  .about-me__list {
    width: 400px;
  }
  .about-content {
    flex: 2;
    margin-right: 2.5em;
  }
  .about-me p {
    width: 60%;
  }
  .about-me blockquote {
    width: 75%;
  }
  /* featured header styles */
  .featured__header {
    display: flex;
    justify-content: space-between;
  }
  .featured__header-left {
    width: 55%;
    margin-right: 3rem;
  }
  .featured__header-right {
    padding-top: 50px;
    width: 450px;
  }
  .featured__header-right .inner {
    padding: 0;
  }
  .featured__header-right img {
    height: 250px;
    float: right;
    padding-right: 1.2em;
  }
  .featured-projects__caption p {
    width: 80%;
  }
}

/* END of  min-width 1152px styles */
@media screen and (min-width: 1440px) {
  .featured-projects {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .featured-projects__module {
    width: 450px;
  }
  .featured-projects__module .inner {
    display: block;
    padding: 0;
  }
  .featured-projects__module .inner .featured-projects__image {
    min-width: 450px;
  }
  .featured-projects__caption p {
    width: 100%;
  }
  .social-icons {
    width: 95%;
  }
}
